import Vue from 'vue'
import App from './App'

//click
Vue.directive('tap', {
    isFn : true,
    acceptStatement:true,
    update : function(fn) {
	    var self = this; //this
	        //directive
	        //self.xxx   self.touchstart()
	        self.tapObj = {}; //tap

	    if(typeof fn !== 'function') {
	    //
	        return console.error('The param of directive "v-tap" must be a function!');
	    }
	    
	    self.handler = function(e) { //directivehandler
	        e.tapObj = self.tapObj; 
	        //tapevent
	        fn.call(self,e);
	    };
	    //tapmove
	    this.el.addEventListener('touchstart',function(e) {
	        self.touchstart(e,self);
	    },false);
	    
	    this.el.addEventListener('touchend',function(e) {
	        self.touchend(e,self,fn);
	    },false); 
	},
    isTap : function() {
	    var tapObj = this.tapObj;
	    //click
	    return this.time < 150 && Math.abs(tapObj.distanceX) < 2 && Math.abs(tapObj.distanceY) < 2;
	},
	touchstart : function(e,self) {
	    var touches = e.touches[0];
	    var tapObj = self.tapObj;
	    tapObj.pageX = touches.pageX;
	    tapObj.pageY = touches.pageY;
	    tapObj.clientX = touches.clientX;
	    tapObj.clientY = touches.clientY;
	    self.time = new Date().getTime();
	},
	touchend : function(e,self) {
	    var touches = e.changedTouches[0];
	    var tapObj = self.tapObj;
	    self.time = new Date().getTime() - self.time;
	    tapObj.distanceX = tapObj.pageX - touches.pageX;
	    tapObj.distanceY = tapObj.pageY - touches.pageY;

	    if (self.isTap(tapObj))
	        self.handler(e);
	}
});

//load
Vue.directive('load', {
    isFn : true,
    acceptStatement:true,
    bind : function() {
         
    },
    update : function(fn) {
	    if(typeof fn !== 'function') {
	    //
	        return console.error('The param of directive "v-tap" must be a function!');
	    }
	    window.addEventListener('load', fn,false) 
	}
});
new Vue({
  el: 'body',
  components: { App }
})



